<script src="../../../config/index.js"></script>
<template>
    <div class="clearfix">
      <div class="operation">
              <div class="clearfix ">

                        <el-form label-width="80px">
                                <el-col :span="5">
                                  <el-form-item label="店铺分类">
                                  <!-- <el-autocomplete v-model="tempData.categoryName"
                                    :fetch-suggestions="searchCategory"
                                    placeholder="输入店铺分类模糊查询"
                                    value-key="categoryName"
                                    debounce=1000>
                                  </el-autocomplete> -->
                                  <el-select
                                  v-model="tempData.categoryName"
                                  filterable
                                  remote
                                  placeholder="输入店铺分类模糊查询"
                                  :loading="loading2"
                                  @focus="searchCategory('')"
                                  :remote-method="searchCategory">
                                  <el-option
                                    v-for="item in categoryList"
                                    :key="item.categoryCode"
                                    :value="item.categoryCode"
                                    :label="item.categoryName"
                                  ></el-option>
                                  </el-select>
                                  </el-form-item>
                                </el-col>
                                <el-col :span="5">
                                  <el-form-item label="店铺分区">
                                    <el-autocomplete
                                    v-model="tempData.areaName"
                                    :fetch-suggestions="serZoneData"
                                    placeholder="输入店铺分区模糊查询"
                                    value-key="areaName"
                                    :debounce=2000>
                                    </el-autocomplete>
                                  </el-form-item>
                                </el-col>
                                <el-col :span="5">
                                  <el-form-item label="开启状态">
                                    <el-select v-model="tempData.onOff" clearable>
                                      <el-option label="全部" value="">全部</el-option>
                                      <el-option label="已开启" value="0"></el-option>
                                      <el-option label="未开启" value="1"></el-option>
                                    </el-select>
                                  </el-form-item>
                                </el-col>
                              <el-col :span="5">
                                  <el-form-item label="店铺名称" v-model="tempData.shopName">
                                      <el-input  placeholder="输入店铺名称模糊查询" >
                                      </el-input>
                                  </el-form-item>
                                </el-col>
                                <el-col :span="4" style="padding-left:40px">
                                  <el-button type="primary" @click="search" icon="el-icon-search">查询</el-button>
                                </el-col>
                        </el-form>
                </div>
                            <el-button type="primary" @click="oneKey(1)">一键关闭</el-button>
                            <el-button type="primary" @click="oneKey(0)">一键开启</el-button>
                            <el-button type="primary" @click="excelBusShop" class="addBatchBtn">
                              <router-link to="/gober/addBatch">批量上传店铺</router-link>
                            </el-button>
      </div>
      <div class="table">
                    <el-table
                        empty-text="暂无数据"
                        border
                        :data="dataList"
                        style="width: 100%;margin-top:20px;"
                         @selection-change="handleSelectionChange"
                        >
                      <el-table-column
                        type="selection"
                        width="55">
                      </el-table-column>
                        <el-table-column
                        fixed
                        label="序号"
                        width="80"
                        align="center"
                        type="index" >
                        </el-table-column>
                        <el-table-column
                        prop="shopName"
                        label="店铺名称"
                        width="200"
                         align="center" >
                        </el-table-column>
                        <el-table-column
                        prop="address"
                        label="店铺地址"
                        width="250"
                        align="center" >
                        </el-table-column>
                        <el-table-column
                        prop="businessTime"
                        label="营业时间"
                        width="200"
                         align="center" >
                        </el-table-column>
                        <el-table-column
                        prop="categoryName"
                        label="店铺分类"
                        width="220"
                         align="center" >
                        </el-table-column>
                        <el-table-column
                        prop="areaName"
                        label="店铺分区"
                        width="200"
                         align="center" >
                        </el-table-column>
                        <el-table-column
                        prop="isOpen"
                        label="店铺状态"
                        width="120"
                         align="center" >
                        <template slot-scope="scope">
                            <el-switch
                             v-model="scope.row.onOff"
                              active-color="#13ce66"
                              inactive-color="#ff4949"
                              :active-value="0"
                              :inactive-value="1"
                              @change="closeAndopen(scope.row.id,scope.row.onOff,scope.$index)">
                            </el-switch>
                        </template>
                        </el-table-column>
                </el-table>
            </div>
             <pagination :current-page="page.currentPage"
               :page-size="page.pageSize"
               :total="page.pageNum"
               @sizeChange="sizeChange"
               @currentChange="currentChange"
            >
            </pagination>
    </div>
</template>

<script>
import { storeMessageList, storeMessageOffAndOn, store, excelBusShop } from "@/api/index.js";
import pagination from "@/components/pagination";
export default {
  components: {
    pagination
  },
  data() {
    return {
      //店铺分类列表
      categoryList:[],
      loading2: false,
      //店铺分类下拉加载时间
      categoryTime: "",
      //店铺分区下拉加载时间
      zoneTime: "",
      //一键队列
      oneKeylist: [],
      //一键操作队列
      operList: [],
      loading: false,
      dataList: [],
      //搜索时的临时数据
      tempData: {
        areaName: null,
        categoryName: null,
        shopName: null,
        onOff: null
      },
      //搜索时的数据
      serData: {},
      //分页信息
      page: {
        pageNum: 10,
        currentPage: 1,
        pageSize: 5
      }
    };
  },
  methods: {
    //加载数据
    initData(page, size) {
      this.loading = true;
      storeMessageList({
        page: page || this.page.currentPage,
        size: size || this.page.pageSize,
        areaName: this.serData.areaName,
        categoryName: this.serData.categoryName,
        onOff: this.serData.onOff,
        shopNme: this.serData.shopNme
      }).then(res => {
        this.dataList = res.rows;
        this.page.pageNum = res.count;
      });
    },
    excelBusShop () {

    },
    //页数改变时的回调
    currentChange(val) {
      this.page.currentPage = val;
      this.initData();
    },
    //每页总数改变时的回调
    sizeChange(val) {
      this.page.pageSize = val;
      this.initData(1);
    },
    //开关店铺
    closeAndopen(id, isOpen, index) {
      this.loading = true;
      store
        .openSwitch({
          id,
          onOff: isOpen
        })
        .then(res => {
          // if (res.code == 0) {
          //   this.$message.success("修改成功");
          // } else {
          //   this.$message.error("修改失败");
          //   if (index != "undefined") {
          //     this.dataList[index].onOff = isOpen == 1 ? 0 : 1;
          //   }
          // }
          setTimeout(() => {
            this.loading = false;
          }, 500);
        });
    },
    //多项操作时storeMessageSearch
    //id储存的队列
    handleSelectionChange(list) {
      this.operList.lenth = 0;
      this.oneKeylist.length = 0;
      this.operList = list;
      list.forEach(item => {
        this.oneKeylist.push(item.id);
      });
    },
    //一键操作
    oneKey(number) {
      if (this.operList.length == 0 || this.oneKeylist.length == 0) {
        this.$message.error("未选取操作对象");
        return;
      }
      this.loading = true;
      let upList = [];
      this.oneKeylist.forEach(item => {
        upList.push({
          id: item,
          onOff: number
        });
      });
      store
        .storeMessageOffAndOn(upList)
        .then(res => {
          if (res.code == "0") {
            this.$message.success("一键操作成功");
            //操作成功本地改变数据,避免多次请求接口
            this.operList.forEach(item => {
              item.onOff = number;
            });
          } else {
            this.$message.success("一键操作失败");
          }
        })
        .then(() => {
          setTimeout(() => {
            this.loading = false;
          }, 500);
        });
    },
    //限流拦截
    throttle(fn, time) {
      time = time || 1000;
      var clock
      return function() {
        clearTimeout(clock);
        var arg = arguments;
        clock = setTimeout(function(){
          fn()
        }, time);
      };
    },
    searchCategory (v) {
      (this.throttle(()=>{
        store.storeCategory({
          page:1,
          size:50,
          categoryName:v
        }).then((res)=>{
          this.categoryList=res.rows
        })
      }, 2000))();
    },
    //店铺分区下拉库搜索
    serZoneData(areaName, cb) {
      var result,clock
      store
        .storeZoom({
          page: 1,
          size: 50,
          areaName
        })
        .then(res => {
          result=res.rows
          clearTimeout(clock)
          clock = setTimeout(() => {
            cb(result);
          }, 1000 * Math.random());
        });
    },
    //条件筛选店铺
    search() {
      this.serData = Object.create(this.tempData);
      this.initData(1);
    }
  },
  beforeMount() {
    this.initData();
  }
};
</script>

<style>
.addBatchBtn a {
    color: #ffffff;
}
.content {
  /*min-width: 1200px;*/
  background: rgb(255, 255, 255);
  padding: 26px 30px;
  position: relative;
  /*min-height: 800px;*/
}
h4 {
  text-align: left;
  color: #333333;
  font-size: 18px;
  font-family: PingFang-SC-Bold;
  padding-bottom: 25px;
  border-bottom: 1px solid #e1e1e1;
}
</style>



